import React from 'react';
import {
  Card,
  CardBody,
  CardSubtitle,
  CardText,
  CardTitle,
  Row,
} from 'reactstrap';
import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import RecentHack from '../../../components/common/RecentHack';
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import hacks from '../../../data/hacks';

const ViewChallenges = ({ match }) => (
  <>
    <Row>
      <Colxx xxs="12">
        <Breadcrumb heading="menu.challenges" match={match} />
        <Separator className="mb-5" />
      </Colxx>
    </Row>
    <Row>
      <Colxx xxs="12" className="mb-4">
        <Row>
          <Colxx xxs="12" md="8">
            <Card className="mb-4">
              <CardBody>
                <CardTitle>
                  <h2 className="lead">EtherPunk 2021</h2>
                  <p>An ETHIndia Hackathon.</p>
                </CardTitle>
                <CardSubtitle>
                  <p>Registration Closes in 2 days.</p>
                </CardSubtitle>
              </CardBody>
            </Card>
          </Colxx>

          <Colxx xxs="12" md="4">
            <Card className="mb-4" color="warning">
              <CardBody>
                <CardTitle>
                  <h2 className="lead">Organizing a Hackathon?</h2>
                </CardTitle>
                <CardText className="text-white">
                  <p>
                    Okay Organize it. Lorem ipsum dolor sit amet, adipiscing
                    elit, eiusmod incididunt ut labore aliqua.
                  </p>
                </CardText>
              </CardBody>
            </Card>
          </Colxx>

          <Colxx xxs="12" className="mb-4">
            <Row>
              {hacks.map((hack) => {
                return (
                  <Colxx xxs="12" lg="4" className="mb-4" key={hack.id}>
                    <RecentHack data={hack} />
                  </Colxx>
                );
              })}
            </Row>
          </Colxx>
        </Row>
      </Colxx>
    </Row>
  </>
);

export default ViewChallenges;
